import React, { useState, useCallback, useEffect } from 'react';
import { NewsArticle } from '../../types';
import { ViewMode, useViewMode } from '../../contexts/ViewModeContext';
import ArticleView from './ArticleView';
import NotesEditor from './NotesEditor';

interface ArticleDetailModalProps {
  article: NewsArticle | null;
  isOpen: boolean;
  onClose: () => void;
  onNavigate?: (direction: 'prev' | 'next') => void;
  hasPrev?: boolean;
  hasNext?: boolean;
}

const ArticleDetailModal: React.FC<ArticleDetailModalProps> = ({
  article,
  isOpen,
  onClose,
  onNavigate,
  hasPrev = false,
  hasNext = false
}) => {
  const { viewMode } = useViewMode();
  const [showNotes, setShowNotes] = useState(false);
  const [notes, setNotes] = useState<any[]>([]);

  // Load notes from localStorage when article changes
  useEffect(() => {
    if (article) {
      const savedNotes = localStorage.getItem(`article-notes-${article.id}`);
      if (savedNotes) {
        try {
          const parsedNotes = JSON.parse(savedNotes);
          setNotes(parsedNotes);
        } catch (error) {
          console.error('Error loading notes:', error);
          setNotes([]);
        }
      } else {
        setNotes([]);
      }
    }
  }, [article]);

  // Save notes to localStorage
  const handleNotesChange = useCallback((newNotes: any[]) => {
    setNotes(newNotes);
    if (article) {
      localStorage.setItem(`article-notes-${article.id}`, JSON.stringify(newNotes));
    }
  }, [article]);

  // Handle keyboard events
  useEffect(() => {
    const handleKeyPress = (event: KeyboardEvent) => {
      if (!isOpen) return;

      switch (event.key) {
        case 'Escape':
          onClose();
          break;
        case 'ArrowLeft':
          if (hasPrev && onNavigate) {
            onNavigate('prev');
          }
          break;
        case 'ArrowRight':
          if (hasNext && onNavigate) {
            onNavigate('next');
          }
          break;
        case 'n':
        case 'N':
          if (event.ctrlKey || event.metaKey) {
            event.preventDefault();
            setShowNotes(prev => !prev);
          }
          break;
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [isOpen, onClose, onNavigate, hasPrev, hasNext]);

  // Close modal when clicking outside
  const handleBackdropClick = (event: React.MouseEvent) => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  // Copy article link to clipboard
  const handleShare = async () => {
    if (article) {
      const url = `${window.location.origin}${window.location.pathname}?article=${article.id}`;
      try {
        await navigator.clipboard.writeText(url);
        // You could add a toast notification here
        alert('Link copied to clipboard!');
      } catch (err) {
        console.error('Failed to copy link:', err);
      }
    }
  };

  // Print article
  const handlePrint = () => {
    window.print();
  };

  if (!isOpen || !article) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-50">
      <div className="relative w-full max-w-6xl h-full max-h-[90vh] bg-white dark:bg-gray-900 rounded-lg shadow-2xl flex flex-col">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700">
          <div className="flex items-center space-x-4">
            <h2 className="text-xl font-bold text-gray-900 dark:text-white">
              {article.title}
            </h2>
            <div className="flex items-center space-x-2">
              <span className="px-2 py-1 text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200 rounded">
                {article.topic}
              </span>
              {article.subTopic && (
                <span className="px-2 py-1 text-xs font-medium bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200 rounded">
                  {article.subTopic}
                </span>
              )}
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            {/* Share button */}
            <button
              onClick={handleShare}
              className="p-2 text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-white transition-colors"
              title="Share article"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
              </svg>
            </button>

            {/* Print button */}
            <button
              onClick={handlePrint}
              className="p-2 text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-white transition-colors"
              title="Print article"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 17h2a2 2 0 002-2v-4a2 2 0 00-2-2H5a2 2 0 00-2 2v4a2 2 0 002 2h2m2 4h6a2 2 0 002-2v-4a2 2 0 00-2-2H9a2 2 0 00-2 2v4a2 2 0 002 2zm8-12V5a2 2 0 00-2-2H9a2 2 0 00-2 2v4h10z" />
              </svg>
            </button>

            {/* Notes toggle */}
            <button
              onClick={() => setShowNotes(prev => !prev)}
              className={`p-2 transition-colors ${
                showNotes 
                  ? 'text-blue-600 dark:text-blue-400' 
                  : 'text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-white'
              }`}
              title="Toggle notes (Ctrl+N)"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
              </svg>
            </button>

            {/* Close button */}
            <button
              onClick={onClose}
              className="p-2 text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-white transition-colors"
              title="Close (Esc)"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="flex-1 flex overflow-hidden">
          {/* Article content */}
          <div className={`flex-1 overflow-y-auto ${showNotes ? 'w-2/3' : 'w-full'} transition-all duration-300`}>
            <div className="p-6">
              {/* Article metadata */}
              <div className="mb-6 p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                  <div>
                    <span className="font-medium text-gray-700 dark:text-gray-300">Date:</span>
                    <span className="ml-2 text-gray-600 dark:text-gray-400">
                      {new Date(article.date).toLocaleDateString()}
                    </span>
                  </div>
                  <div>
                    <span className="font-medium text-gray-700 dark:text-gray-300">Importance:</span>
                    <span className="ml-2 text-gray-600 dark:text-gray-400">
                      {'⭐'.repeat(article.importance)} ({article.importance}/5)
                    </span>
                  </div>
                  <div>
                    <span className="font-medium text-gray-700 dark:text-gray-300">Read time:</span>
                    <span className="ml-2 text-gray-600 dark:text-gray-400">
                      ~{Math.ceil(article.content.detailed.split(' ').length / 200)} min
                    </span>
                  </div>
                </div>
              </div>

              {/* Article view with view mode toggles */}
              <ArticleView 
                article={article}
                initialMode={viewMode}
              />
            </div>
          </div>

          {/* Notes panel */}
          {showNotes && (
            <div className="w-1/3 border-l border-gray-200 dark:border-gray-700">
              <div className="p-4 border-b border-gray-200 dark:border-gray-700">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Notes
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Press Ctrl+N to toggle notes panel
                </p>
              </div>
              <div className="h-full">
                <NotesEditor
                  articleId={article.id}
                  initialNotes={notes}
                  onNotesChange={handleNotesChange}
                  className="h-full"
                />
              </div>
            </div>
          )}
        </div>

        {/* Navigation footer */}
        {(hasPrev || hasNext) && (
          <div className="flex items-center justify-between p-4 border-t border-gray-200 dark:border-gray-700">
            <button
              onClick={() => onNavigate?.('prev')}
              disabled={!hasPrev}
              className="flex items-center space-x-2 px-4 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 bg-gray-100 dark:bg-gray-700 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
              <span>Previous</span>
            </button>

            <div className="text-sm text-gray-500 dark:text-gray-400">
              Use arrow keys to navigate
            </div>

            <button
              onClick={() => onNavigate?.('next')}
              disabled={!hasNext}
              className="flex items-center space-x-2 px-4 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 bg-gray-100 dark:bg-gray-700 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              <span>Next</span>
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
              </svg>
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default ArticleDetailModal; 